import PropTypes from "prop-types";
import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { getProductCartQuantity } from "../../helpers/product";
import Rating from "./sub-components/ProductRating";
import { addToCart } from "../../store/slices/cart-slice";
import { addToWishlist } from "../../store/slices/wishlist-slice";
import { addToCompare } from "../../store/slices/compare-slice";
import Chip from '@mui/material/Chip';
import "./ProductDesc.css"
const ProductDescriptionInfo = ({
  product,
  discountedPrice,
  currency,
  finalDiscountedPrice,
  finalProductPrice,
  cartItems,
  wishlistItem,
  compareItem,
}) => {
  const colors=[
    "#747472",
    "#b7afe6",
    "#d8e4f0",
    "#b2b6b8",
    "#f9d4c2",
    "#ef5e00",
    "#c6383f",
    "#85acd0",
  ]
  const Storage = [
    "32 Gb",
    "64 Gb",
    "256 Gb",
    "512 Gb",
  ]
  const dispatch = useDispatch();
  const [selectedProductColor, setSelectedProductColor] = useState(
    product?.variation ? product?.variation[0]?.color : ""
  );
  const [selectedProductSize, setSelectedProductSize] = useState(
    product?.variation ? product?.variation[0]?.size[0]?.name : ""
  );
  const [productStock, setProductStock] = useState(
    product?.variation ? product?.variation[0]?.size[0]?.stock : product?.stock
  );
  const [quantityCount, setQuantityCount] = useState(1);
  const [AppleCare, setAppleCare] = useState(null);
  const productCartQty = getProductCartQuantity(
    cartItems,
    product,
    selectedProductColor,
    selectedProductSize
  );

  return (
    <div className="product-details-content ml-70">
      <h2>{product?.name}</h2>
      <div className="product-details-price">
        {product?.sale_price !== null ? (
          <Fragment>
            <span>{"Rs. "  + product?.sale_price}</span>{" "}
            <span className="old">
              {"Rs. "  + product?.price}
            </span>
          </Fragment>
        ) : (
          <span>{"Rs. "  + product?.price} </span>
        )}
      </div>
      {product?.rating && product?.rating > 0 ? (
        <div className="pro-details-rating-wrap">
          <div className="pro-details-rating">
            <Rating ratingValue={product?.rating} />
          </div>
        </div>
      ) : (
        ""
      )}
      <div className="pro-details-list">
        {/* <p>{product?.description}</p> */}
        <p dangerouslySetInnerHTML={{ __html: product?.description }}/>
      </div>
    {/* colors */}
      <div>
      <h3>Colors</h3>
      <div style={{width:"250px",display:"flex",gap:"10px",flexWrap:"wrap"}}>  {
    colors?.map((item)=>{
      return(
        <div style={{backgroundColor:item,height:"20px",width:"20px",borderRadius:"50%"}}>
        </div>
      )
    })
    } </div>
    </div>
  
  <div style={{marginTop:"40px"}}>
  <h3>Storage</h3>
    <div style={{display:"flex",gap:"10px",flexWrap:"wrap"}}>
       {Storage?.map((item)=>{
       return(
      <Chip label={item} />)
       })}
       </div>
  </div>

<div onClick={()=>setAppleCare(1)} className={AppleCare==1?"AppleCareSelected":"AppleServiceContainer"}>
<div className="AppleServiceHeader"> 
<div className="AppleServiceImg">
<img src="https://www.myimaginestore.com/pub/crm/assets/images/applecare_plus_icon.png" width="56px"/>
<h5>AppleCare+</h5>
</div> 
<div>
<h5>Rs 19,021</h5>
</div>
</div>
<hr/>
<ul class="acplus-section_list"><li>Enjoy 2 years of peace of mind when you buy AppleCare+ for iPhone.</li><li> Apple-Certified Service and support coverage</li><li>Accidental Damage Protection*</li><li>Battery service coverage</li></ul>
<div class="LearnMoreBtn"><a>Learn More</a></div>
</div>

<div onClick={()=>setAppleCare(2)} className={AppleCare==2?"AppleCareSelected":"AppleServiceContainer"}>
<div className="AppleServiceHeader"> 
<div className="AppleServiceImg">
<img src="https://www.myimaginestore.com/pub/crm/assets/images/applecare_plus_icon.png" width="56px"/>
<h5> Protect+ AppleCare Services</h5>
</div> 
<div>
<h5>Rs 19,021</h5>
</div>
</div>
<hr/>
<ul class="acplus-section_list"><li>Enjoy 2 years of peace of mind when you buy AppleCare+ for iPhone.</li><li> Apple-Certified Service and support coverage</li><li>Accidental Damage Protection*</li><li>Battery service coverage</li></ul>
<div class="LearnMoreBtn"><a>Learn More</a></div>
</div>

      {product?.variation ? (
        <div className="pro-details-size-color">
          <div className="pro-details-color-wrap">
            <span>Color</span>
            <div className="pro-details-color-content">
              {product?.variation.map((single, key) => {
                return (
                  <label
                    className={`pro-details-color-content--single ${single.color}`}
                    key={key}
                  >
                    <input
                      type="radio"
                      value={single.color}
                      name="product-color"
                      checked={
                        single.color === selectedProductColor ? "checked" : ""
                      }
                      onChange={() => {
                        setSelectedProductColor(single.color);
                        setSelectedProductSize(single.size[0].name);
                        setProductStock(single.size[0].stock);
                        setQuantityCount(1);
                      }}
                    />
                    <span className="checkmark"></span>
                  </label>
                );
              })}
            </div>
          </div>
          
          <div className="pro-details-size">
            <span>Size</span>
            <div className="pro-details-size-content">
              {product?.variation &&
                product?.variation.map(single => {
                  return single.color === selectedProductColor
                    ? single.size.map((singleSize, key) => {
                        return (
                          <label
                            className={`pro-details-size-content--single`}
                            key={key}
                          >
                            <input
                              type="radio"
                              value={singleSize.name}
                              checked={
                                singleSize.name === selectedProductSize
                                  ? "checked"
                                  : ""
                              }
                              onChange={() => {
                                setSelectedProductSize(singleSize.name);
                                setProductStock(singleSize.stock);
                                setQuantityCount(1);
                              }}
                            />
                            <span className="size-name">{singleSize.name}</span>
                          </label>
                        );
                      })
                    : "";
                })}
            </div>
          </div>    
        </div>
      ) : (
        ""
      )}
      {product?.affiliateLink ? (
        <div className="pro-details-quality">
          <div className="pro-details-cart btn-hover ml-0">
            <a
              href={product?.affiliateLink}
              rel="noopener noreferrer"
              target="_blank"
            >
              Buy Now
            </a>
          </div>
        </div>
      ) : (
        <div className="pro-details-quality">
          <div className="cart-plus-minus">
            <button
              onClick={() =>
                setQuantityCount(quantityCount > 1 ? quantityCount - 1 : 1)
              }
              className="dec qtybutton"
            >
              -
            </button>
            <input
              className="cart-plus-minus-box"
              type="text"
              value={quantityCount}
              readOnly
            />
            <button
              onClick={() =>
                setQuantityCount(
                  quantityCount < productStock - productCartQty
                    ? quantityCount + 1
                    : quantityCount
                )
              }
              className="inc qtybutton"
            >
              +
            </button>
          </div>
          <div className="pro-details-cart btn-hover">
            {product?.stock_quantity && product?.stock_quantity > 0 ? (
              <button
                onClick={() =>
                  dispatch(addToCart({
                    ...product,
                    quantity: quantityCount,
                    selectedProductColor: selectedProductColor ? selectedProductColor : product?.selectedProductColor ? product?.selectedProductColor : null,
                    selectedProductSize: selectedProductSize ? selectedProductSize : product?.selectedProductSize ? product?.selectedProductSize : null
                  }))
                }
                disabled={productCartQty >= productStock}
              >
                {" "}
                Add To Cart{" "}
              </button>
            ) : (
              <button disabled>Out of Stock</button>
            )}
          </div>
          <div className="pro-details-wishlist">
            <button
              className={wishlistItem !== undefined ? "active" : ""}
              disabled={wishlistItem !== undefined}
              title={
                wishlistItem !== undefined
                  ? "Added to wishlist"
                  : "Add to wishlist"
              }
              onClick={() => dispatch(addToWishlist(product))}
            >
              <i className="pe-7s-like" />
            </button>
          </div>
          <div className="pro-details-compare">
            <button
              className={compareItem !== undefined ? "active" : ""}
              disabled={compareItem !== undefined}
              title={
                compareItem !== undefined
                  ? "Added to compare"
                  : "Add to compare"
              }
              onClick={() => dispatch(addToCompare(product))}
            >
              <i className="pe-7s-shuffle" />
            </button>
          </div>
        </div>
      )}
      {product?.category ? (
        <div className="pro-details-meta">
          <span>Categories :</span>
          <ul>
            {product?.category.map((single, key) => {
              return (
                <li key={key}>
                  <Link to={process.env.PUBLIC_URL + "/shop-grid-standard"}>
                    {single}
                  </Link>
                </li>
              );
            })}
          </ul>
        </div>
      ) : (
        ""
      )}
      {product?.tag ? (
        <div className="pro-details-meta">
          <span>Tags :</span>
          <ul>
            {product?.tag.map((single, key) => {
              return (
                <li key={key}>
                  <Link to={process.env.PUBLIC_URL + "/shop-grid-standard"}>
                    {single}
                  </Link>
                </li>
              );
            })}
          </ul>
        </div>
      ) : (
        ""
      )}

      {/* <div className="pro-details-social">
        <ul>
          <li>
            <a href="//facebook.com">
              <i className="fa fa-facebook" />
            </a>
          </li>
          <li>
            <a href="//dribbble.com">
              <i className="fa fa-dribbble" />
            </a>
          </li>
          <li>
            <a href="//pinterest.com">
              <i className="fa fa-pinterest-p" />
            </a>
          </li>
          <li>
            <a href="//twitter.com">
              <i className="fa fa-twitter" />
            </a>
          </li>
          <li>
            <a href="//linkedin.com">
              <i className="fa fa-linkedin" />
            </a>
          </li>
        </ul>
      </div> */}
    </div>
  );
};

ProductDescriptionInfo.propTypes = {
  cartItems: PropTypes.array,
  compareItem: PropTypes.shape({}),
  currency: PropTypes.shape({}),
  discountedPrice: PropTypes.number,
  finalDiscountedPrice: PropTypes.number,
  finalProductPrice: PropTypes.number,
  product: PropTypes.shape({}),
  wishlistItem: PropTypes.shape({})
};

export default ProductDescriptionInfo;
